// import {Link} from "gatsby"
import React from "react"

const About = () => {
    return (
        

    <div className="py-20 px-4 min-h-3/4 dark:bg-gray-800" id="about">
    <div className="mx-auto max-w-6xl flex flex-col md:flex-row">
        <h2 className="mr-8 w-full md:w-1/3 text-3xl font-extrabold leading-9 dark:text-white">
            About Me &nbsp; <span aria-label="jsx-a11y/accessible-emoji" role="img">üßëüèΩ</span>
        </h2>
        <dl className="w-full md:w-2/3">
            <dt className="mb-4">
                <h3 className="text-xl font-semibold dark:text-white">
                    Personal Life
                </h3>
            </dt>
            <dd className="mb-16 dark:text-white">
                <p>
                    I am an enthusiastic and passionate High School Junior who loves loves to learn new things! I enjoy
                    spending time with my friends and family. I have a black belt, play basketball casually, and love
                    to go the gym. I also volunteer as a tutor and love to meet new people!
                </p>
            </dd>
            <dt className="mb-4 dark:text-white">
                <h3 className="text-xl font-semibold">
                   Professional Life
                </h3>
            </dt>
            <dd className="mb-16 dark:text-white">
                <p>
                    I specialize in computer programming, robotics, information technology, and 
                    computer hardware. I am a 5x Hackathon Winner, have experience developing Android, IOS, and
                    Web Applications, and have experience with Agile Development. In addition, I have won awards
                    from Future Business Leaders of America, a national organization that holds selective
                    competitions, and Morgan State University.
                </p>
            </dd>
            <dt className="mb-4 dark:text-white">
                <h3 className="text-xl font-semibold">
                    Goal
                </h3>
            </dt>
            <dd className="mb-16 dark:text-white">
                <p>
                My dream is to use my skills and resources available to create a product that will help people and change the world. Therefore, I am working hard towards learning as much as possible and initiating an impact on our world.
                </p>
            </dd>
        </dl>
    </div>
</div>


    )
}

export default About